/* -------------------------------- 

Back to top 

-------------------------------- */
.cd-top {
  position: fixed;
  z-index: 99;
  bottom: 40px;
  right: 5%;
  display: inline-block;
  height: 48px;
  width: 48px;
  /* replace text with image */
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  background: rgba(0, 0, 0, 0.6) url(../img/cd-icon-arrow.svg) no-repeat center
    center;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.3s, visibility 0.3s;
  transition: opacity 0.3s, visibility 0.3s;
}
.cd-top.is-visible {
  /* the button becomes visible */
  visibility: visible;
  opacity: 1;
}
.cd-top:hover {
  background-color: black;
  opacity: 1;
}

@media screen and (max-width: 369px) {
  .cd-3d-portfolio-navigation {
    margin-top: 50px;
    padding: 0px 2px;
  }
  .cd-3d-portfolio-navigation li a {
    border: 1px solid #000;
    font-size: 14px;
    padding: 5px 5px;
  }
}
